import { createWebHistory, createRouter } from "vue-router";

import LeftSideBar from './components/LeftSideBar.vue'


import Home from './components/Home.vue'
import List01 from './components/List.vue'
import List02 from './components/List02.vue'
import Detail from './components/Detail.vue'

import Error404 from './components/404Error.vue'

  import Author from './components/Author.vue'
  import Comment from './components/Comment.vue'

const routes = [

  //리다이렉트
  // {path:'/list02', redirect: '/'},

  // 별칭 : list02를 방문했지만 list01을 방문한것처럼 매칭
  // {path:'/list01', component : List01, alias:'/list02'},

  {
    path: "/",
    components: {
      default: Home,
      a: Comment,
      sidebar: LeftSideBar,
    },

  },

  {
    path: "/list01",
    components: {
      default: List01,
      a: Comment,
      sidebar: LeftSideBar,
    },

  },
  {
    path: "/list02",
    component: List02,
    name:'list02_name',
  },

  {
                            // :파라미터 문법
    path: "/detail/:id",//작명 :아무글자입력해도 나옴. 숫자만 찾아주는 정규식- (\\d+)
    components: {
      default: Detail,
      a: Comment,
      sidebar: LeftSideBar,
    },


    //nested routes 만들기 
    children:[
      {
        path: "author", //상대경로로 적어야됨 -> detail/0/author
        component: Author,
      },
      {
        path: "comment", //detail/0/comment
        component: Comment,
      },
    ],
  },

  {
    path: "/:sdfsdfsadr(.*)",//작명 아무글자 입력시 404에러. 모든문자라는 정규식 문법- (.*)
    component: Error404,
  },


];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;